  font-size: 1.1vw;
  font-family: "Montserrat Bold";
  color: #db3b60;
  padding-top: 2vh;
}

.about-body-text p {
  padding-bottom: 2vh;
}
.about-text-2022 {
  margin: 10vw;
  padding-top: 2vh;
  padding-bottom: 2vh;
}

.about-row {
  display: flex;
  flex-direction: row;
}

.about-img {
  width: 10vw;
  margin: 0;
}

.about-img2 {
  width: 10vw;
  margin: 0;
  padding-left: 5vw;
  padding-top: 5vh;
}

.home p {
  width: 85%;
  margin-left: 70px;
  line-height: 1.5em;
  display: inline-block;
  margin-top: 15px;
}
.home h1 {
  display: inline-block;
  margin-bottom: 0px;
  margin-top: 10px;
  font-size: 3.5em;
  text-decoration: underline dotted #ffd3de;
}
.home {
  margin-top: 80px;
  margin-left: 130px;
}
.home img {
  height: 100%;
  max-height: 50px;
  width: 100%;
  max-width: 50px;
  margin-right: 10px;
}
/* Details Page */
.img-gallery {
  float: left;
  height: 510px;
  width: 728px;
}

.gallery {
  overflow: hidden;
  padding-top: 3vh;
  margin: 0 auto;
}
.archive {
  overflow: hidden;
  margin: 0 auto;
}
.details-title h1 {
  text-align: center;
  font-size: 3vw;
}

.details-title p {
  text-align: center;
  font-size: 1vw;
}

.details-header {
  width: 1000px;
  margin: auto;
  margin-top: 60px;
}
.detailsbody p {
  font-size: 16px;
  white-space: pre-line;
  line-height: 1.6;
}
.detailsbody {
  margin: 40px 300px 120px 300px;
}
.details-header h3 {
  margin-top: 0px;
  margin-bottom: 0px;
}
.col {
  width: 100%;
  display: flex;
  flex-direction: column;
}
.details-image {
  width: 425px;
  height: auto;
  margin-right: 60px;
}
.details-image img {
  width: 430px;
  height: auto;
  margin-right: 40px;
  border-radius: 25px;
  border: dotted #db3b60;
  padding: 5px;
}
.details-header h1 {
  font-size: 48px;
  font-family: "Montserrat Medium";
  margin-bottom: 10px;
}
.detailsbody h1 {
  font-size: 48px;
  font-family: "Montserrat Medium";
  margin-bottom: 10px;
}
.details-header h2 {
  margin-top: 20px;
  margin-bottom: 10px;
  font-size: 24px;
  font-family: "Montserrat";
}
.detailsbody h2 {
  margin-top: 20px;
  margin-bottom: 10px;
  font-size: 24px;
  font-family: "Montserrat";
}
.QnA h2 {
  margin-top: 40px;
}
.detailsbody {
  font-size: 18px;
}
.details-header {
  font-size: 18px;
}
.small {
  font-size: 16px;
  font-style: italic;
}
.center {
  text-align: center;
  margin-top: 20px;
}

@media (max-width: 900px) {
  .details-header {
    width: 760px;
  }
  .column {
    flex: 100%;
  }

  .body {
    padding-left: 20px;
    padding-right: 20px;
  }
  .detailsbody {
    margin-left: 20px;
    margin-right: 20px;
  }
  .details-image img {
    width: 90vw;
    margin-right: 0px;
    margin-left: 0px;
    max-width: 100%;
  }
  .detailsbody .row {
    flex-wrap: wrap;
  }
  .detailsbody h1 {
    margin-top: 20px;
  }
  .home {
    margin-top: 30px;
    margin-left: 20px;
  }
  .gallery h1 {
    width: 100%;
  }
  .home p {
    width: 100%;
  }
  .home p {
    margin-left: 0px;
  }
}

.to-them {
  margin-top: 15vh;
}
.to-them h1 {
  margin-bottom: 5vh;
}

.link-showcase {
  margin-top: 10vh;
  padding-bottom: 10vh;
}

@media (max-width: 768px) {
  .row {
    flex-direction: column;
  }

  .details-header {
    margin-top: 60px;
    margin-right: auto;
    margin-left: auto;
    width: 94vw;
  }
  .navigation {
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
  }

  .navigation .right {
    margin-left: 40px;
    margin-right: 0;
  }

  .navigation .right button {
    margin: 10px 37px 0 0;
  }
}

@media (max-width: 426px) {
  .intro2020 {
    text-align: left;
  }
  .filterlist button {
    font-size: 24px;
    -webkit-text-stroke-width: 0.75px;
  }

  .card2019 .bigcard {
    width: 90vw;
  }
  .gallery h1 {
    font-size: 48px;
  }
  .intro2020 h1 {
    text-align: left;
    font-size: 48px;
  }
  .filterlist button {
    text-align: left;
  }
  .difficultylist button {
    text-align: left;
    font-size: 24px;
    -webkit-text-stroke-width: 0.75px;
  }
  .cover2020 h1 {
    font-size: 3rem;
  }
  .twentyviz h1 {
    font-size: 40px;
  }
}

@media (max-width: 375px) {
  .navigation .left {
    margin-left: 20px;
  }
  .navigation .right {
    margin-left: 20px;
  }
}

/* 2022 */

.navigation2022 {
  background-color: #fff;
  padding: 40px 0 40px 0;
  width: 100%;
  position: sticky;
  top: 0;
}

.navigation2022 .menu {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}

.navigation2022 .left {
  margin-left: 40px;
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.navigation2022 .left .item {
  margin-right: 20px;
  color: #db3b60;
}

.navigation2022 .left .item:hover {
  color: #ff8ba6;
}

.navigation2022 .right button:hover {
  color: #ff8ba6;
}

.navigation2022 .right {
  margin-right: 40px;
}

.navigation2022 .right button {
  padding: 8px 0;
  margin: 0 0 0 37px;
  cursor: pointer;
  color: #db3b60;
}

.navigation2022 .nav-text {
  margin-left: 40px;
  color: #db3b60;
  font-weight: bold;
  font-style: italic;
  text-decoration: underline;
}

